@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html {
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 100;
  scroll-behavior: smooth;
}

*{
  box-sizing:border-box;
}

a {
  color: #8C8987;
}

main {
  min-height: 100vh;
  max-width: 100vw;
  display: grid;
  place-items: center;
  padding:20vh;
}

button, .button {
  font-size:1rem;
  background-color: black;
  color:white;
  min-width:6rem;
  padding:.6rem;
  display: block;
  cursor: pointer;
}

.edit-icons {
  -webkit-transform:scale(1.2);
          transform:scale(1.2);
  cursor: pointer;
  margin-left: 1rem;
  color: #8C8987;
}



.button {
  background: white;
  border: 1px solid #111;
  color: #111;
  padding:.4rem;
  max-width:6rem;
  text-align: center;
  margin-top: 1rem;
}
.project {
    color:#8C8987;
    width:100%;
    min-height: 600px;
    position: relative;
    cursor: pointer;

    overflow:hidden;
}

.project .bilde{
    min-height:600px;
    width:100%;
}

.project .bilde img{
    width:100%;
    min-height:600px;
    object-fit: cover;
    margin:0;
    padding:0;
    -webkit-transform:scale(1.3);
            transform:scale(1.3);
}
.project:hover > .bilde img{
    -webkit-transition:20s ease;
    transition:20s ease;
    -webkit-transform:scale(1.1);
            transform:scale(1.1);
}


.project .project-title{
    position:absolute;
    left:2rem;
    bottom:2rem;
    font-size:2rem;
    color: white;
    font-weight: 600;
}

.admin-icons{
    position:absolute;
    bottom:2rem;
    right:2rem;
    color: #8C8987;
}

@-webkit-keyframes zoomIn{
    from{
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
    to{
        -webkit-transform:scale(100vw,100vh);
                transform:scale(100vw,100vh);
    }
}

@keyframes zoomIn{
    from{
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
    to{
        -webkit-transform:scale(100vw,100vh);
                transform:scale(100vw,100vh);
    }
}
main.projects{
    padding:calc(10vh + .6rem) .6rem;
  }

.projectsContainer {
    display:grid;
    width: 100%;
    grid-template-columns: repeat(auto-fit, minmax(600px, 1fr));
    grid-gap:.6rem;
    gap:.6rem;   
}

.edit-icons-add {
    -webkit-transform:scale(10);
            transform:scale(10);
    fill:rgba(0,0,0,.8);
    cursor: pointer;
    margin-left: 1rem;
    margin-bottom:2rem;
    position: fixed;
    bottom: 4rem;
    right: 4rem;
    z-index:2;
  }
  

@media (max-width: 600px){
    .projectsContainer {
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); 
    }
}
main.project-details {
    position: relative;
    justify-content: center;
    text-align: justify;
    line-height: 1.9;
    margin-bottom: 30px;
    word-break: break-word;
    padding-top: 0;
    overflow: hidden;
}

.parallax{
    background-position: center 40%;
}

.arrow-down {
    width: 100vw;
    text-align: center;
    -webkit-transform: scale(2);
            transform: scale(2);
    position: absolute;
    bottom: 2rem;
    fill: white;
    cursor: pointer;
}

.description p,
.description a,
.project-details .year {
    width: 35%;
    display: block;
    margin: auto;
    font-size: 18px;
    max-width: 360px;
    bottom: 3rem;
}



/* Footer on project text */

.text-footer{
    display:grid;
    width:100%;
    grid-template-columns: 1fr 360px 1fr;
}
.text-footer .middle{
    display:grid;
    grid-auto-flow:column;
    justify-content: space-between;
}

.description h5, .description h3{
    font-size: 15px;
    display:inline;
    margin:0;
    padding:0;
}
.description h5 {
    font-weight: 600;
}
.description h3 {
    font-weight: 100;
}


.project-details .description img {
    width: 100%;
}

.project-details h4 {
    text-align: center;
    font-size: 17px;
    padding-bottom: 1rem;
    
}

.project-details h6 {
    width: 35%;
    display: block;
    margin: auto;
    max-width: 360px;
    font-size: 14px;
}

    .project-details .description p {
    bottom: 4rem;
    }

.pager {
    width: 80vw;
    left: 0;
    bottom: 5vh;
    height: 10vh;
    display: grid;
    grid-auto-flow: column;
    grid-gap: 4rem;
    gap: 4rem;
    align-items: center;
    justify-content: space-between;
    z-index: 1;
}


.project-details .description a {
    padding-top: 3rem;
    width: 10%;
    text-decoration: underline;
}


