main.about{
    display:grid;
    grid-template-columns:1fr 1fr;
    grid-gap:1rem;
    gap:1rem;
    padding:20vh 5vh 5vh 5vh;
    align-items:start;
    height:100vh;
    overflow:hidden;
}

.profile-text{
    max-width:100%;
   
}


.about h1{
    font-size: 9rem;
    padding:0;
    margin:0;
    color: goldenrod;
}
.about h1 span{
    display:block;
}

.about p{
    font-size: 20px;
    font-weight: 100;
    font-family: 'Josefin Sans', sans-serif;   
    word-break: break-word;
    text-align: justify;
    line-height: 1.6; 
    width: 75%;
}

.about img {
    width:100%;
    max-height:100%;
    height:100%;
    object-fit:cover;
    object-position: center top;
}

.space-between{
    text-align: justify;
    justify-content: space;
}

@media(max-width:1000px){
    .about h1{
        font-size: 5rem;
    } 
}


@media(max-width:600px){
    main.about{
        display:grid;
        grid-template-columns:1fr;
        grid-template-rows: 40vh 40vh;
        padding:10vh 0 10vh 0;
    }
    .about .profile-text {
        grid-row-start: 2;
    }

    .about h1{
        font-size: 6.5rem;
        padding:0;
        margin:0;
        color: goldenrod;
        
        
    }
    .about img {
        object-position: center 15%;
    }
    
}



.header-container {
    position: fixed;
    width: 100vw;
    height: 10vh;
    background: #F2F2F2;
    display:grid;
    place-items: center;
    z-index: 3;
}

.burger{
    display: none;
    z-index: 4;
    position: absolute;
}

header {
    position: fixed;
    width: 100vw;
    height: 10vh;
    background: #F2F2F2;
    display:grid;
    grid-auto-flow: column;
    justify-content: start;
    align-items: center;
    grid-gap:1rem;
    gap:1rem;
    z-index: 3;
}

header a {
    text-decoration: none;
    color: #8C8987;
    margin-left: 15px;
}

[aria-current='page'], .active {
    font-weight: bold;
}

header p{
    margin-left: 2rem;
    margin-right: 2rem;
    color: #8C8987;

}

@media (max-width: 1000px){
    header{
        width:50%;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
        grid-auto-flow: row;
        align-content: center;
        justify-content: center;
        min-height: 100vh;
        width: 100vw;
        background:#f2f2f2;
    }
    .navn{
        position: absolute;
        top: 5vh;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }


    .header a {
        font-size: 2rem;
    }

    header.visible{
        -webkit-transform:translateY(0);
                transform:translateY(0);
        -webkit-transition: 1s ease;
        transition: 1s ease;
    }

    .burger{
        display:block;
        cursor: pointer;
        position: absolute;
        left: 0;
        top: 10;
        margin-left: 20px;
    }

    main {
        padding: 20vh 5vh;
    }

}


main.login {
    justify-items: left;
}

.login img {
